import React, { useState } from 'react';
import { Link } from 'react-router-dom';
// import './SelectTheme.css';
import { ThemeProvider } from "styled-components";
import { SevenGalaxiesTheme, GaiaTheme, StrojTheme } from "./themes";
import GlobalTheme from "./globalTheme"
import ChangeBackground from '../../tools/ChangeBackground';
import simbolo_SevenG from './images/Simbolo_SevenG.png';
import simbolo_Gaia from './images/Simbolo_Gaia.png';
import simbolo_Stroj from './images/Simbolo_Stroj.png';


function SelectTheme() {
  const [theme, setTheme] = useState(window.localStorage.getItem("theme"));

  function handleAction(tema) {
    window.localStorage.setItem("theme", tema);

    const localTheme = window.localStorage.getItem("theme");
    localTheme && setTheme(localTheme);

    ChangeBackground(0);
  }

  return (
    <>
      <ThemeProvider theme={theme === "Stroj" ? StrojTheme : (theme === "Gaia" ? GaiaTheme : SevenGalaxiesTheme)}>
        <GlobalTheme />
      </ThemeProvider>
      <div className="theme">
        <div className="theme-container">

          <Link className="theme-sevenG" onClick={() => handleAction('SevenGalaxies')} to="/sevengalaxies-app/game">
            <div className="theme-top">
              <p>ESCOLHA SEU TEMA</p>
            </div>
            <div className="theme-simbolo">
              <img src={simbolo_SevenG} alt="Menos" />
            </div>
            <div className="theme-title">
              <p> {"7G UNIVERSE"}</p>
            </div>
          </Link>
          <Link className="theme-stroj" onClick={() => handleAction('Stroj')} to="/sevengalaxies-app/game">
            <div className="theme-simbolo">
              <img src={simbolo_Stroj} alt="Menos" />
            </div>
            <div className="theme-title">
              <p> {"GALÁXIA STROJ"}</p>
            </div>
          </Link>
          <Link className="theme-gaia" onClick={() => handleAction('Gaia')} to="/sevengalaxies-app/game">
            <div className="theme-simbolo">
              <img src={simbolo_Gaia} alt="Menos" />
            </div>

            <div className="theme-title">
              <p> {"GALÁXIA GAIA"}</p>
            </div>
          </Link>
          {/* <div className="options">
            <button className="action" onClick={() => handleAction('SevenGalaxies')}>
              <div>7G Theme</div>
            </button>
            <button className="action" onClick={() => handleAction('Gaia')}>
              <div>Gaia Theme</div>
            </button>
            <button className="action" onClick={() => handleAction('Stroj')}>
              <div>Stroj Theme</div>
            </button>
          </div> */}
        </div>
      </div>
    </>

  )

}

export default SelectTheme;