import React, { useState } from 'react';
import { FiMenu } from 'react-icons/fi';
import './Toolbar.css';

import SelectTheme from '../pages/SelectTheme/SelectTheme';

function Toolbar() {
  const [escolheTheme, setEscolheTheme] = useState(false);
  // useState(() => {
  //   const localTheme = window.localStorage.getItem("theme");
  //   console.log(localTheme);
  //   if (localTheme)
  //     return false;
  //   else
  //     return true;

  // });


  function OpenModal() {
    window.location.assign('#theme');
    setEscolheTheme(true);
  }

  return (
    <div className="toolbar" >
      <div className="content-toolbar">
        <FiMenu
          className="menu"
          onClick={() => OpenModal()} />
        <SelectTheme
          onClose={() => setEscolheTheme(false)}
          escolheTheme={escolheTheme} />

      </div>

    </div>
  );
}

